import React, { ReactNode } from 'react';
import { IRestProps, mergeRestProps } from '../../common';
import { GuidePanelWrapper, GuidePanelImage, GuidePanelTitle, GuidePanelDescription } from './styled';

export type IGuidePanelProps = {
  /** 标题 */
  title?: string;
  /** 描述 */
  description?: ReactNode;
  /** 图片 */
  image?: string;
} & IRestProps & React.DOMAttributes<HTMLDivElement>;

export const GuidePanel: React.FC<IGuidePanelProps> = (props) => {
  const { title, description, image, ...rest } = props;
  return (
    <GuidePanelWrapper {...mergeRestProps(rest, image ? 'with_img wdn-guide-panel' : 'wdn-guide-panel')}>
      {title && <GuidePanelTitle>{title}</GuidePanelTitle>}
      <GuidePanelDescription>{description}</GuidePanelDescription>
      {image ? <GuidePanelImage src={image} /> : null}
    </GuidePanelWrapper>
  );
};
